const isSheetVale = (str) => {
    str = str.toUpperCase();
    if (str.search(/[\u4E00-\u9FA50-9a-zA-Z]+![A-Za-z]+\$\d+/) != -1)
        return true;
    if (str.search(/[\u4E00-\u9FA50-9a-zA-Z]+!\$[A-Za-z]+\d+/) != -1)
        return true;
    if (str.search(/[\u4E00-\u9FA50-9a-zA-Z]+!\$[A-Za-z]+\$\d+/) != -1)
        return true;
    if (str.search(/[\u4E00-\u9FA50-9a-zA-Z]+![A-Za-z]+\d+/) != -1)
        return true;
    return false;
}

const getSheetVale = (str) => {
    let arr = [];

    let v = /[\u4E00-\u9FA50-9a-zA-Z]+![A-Za-z]+\d+/.exec(str.replace(/\$/g, ''));
    let v2 = /[\u4E00-\u9FA50-9a-zA-Z]+![A-Za-z]+\d+:[A-Za-z]+\d+/.exec(str.replace(/\$/g, ''));


    if (v) {
        for (let i = 0; i < v.length; i++) {
            arr.push(v[i]);
        }
    }

    if (v2) {
        for (let i = 0; i < v2.length; i++) {
            arr.push(v2[i]);
        }
    }


    return arr;
};

function angleFunc(start, end) {
    let diff_x = end.x - start.x,
        diff_y = end.y - start.y;
    return 360 * Math.atan(diff_y / diff_x) / (2 * Math.PI);
}

const positionAngle = (x1, x2, y1, y2) => {
    let angle = 0;
    let af = Math.abs(angleFunc({x: x1, y: y1}, {x: x2, y: y2}));

    if (x1 < x2 && y1 < y2) {
        angle = 1;
    } else if (x1 > x2 && y1 < y2) {
        angle = 2;
    } else if (x1 < x2 && y1 > y2) {
        angle = 3;
    } else if (x1 > x2 && y1 > y2) {
        angle = 4;
    }

    if (angle == 1 && af < 45) {
        angle = 3;
        return angle;
    } else if (angle == 2 && af > 30) {
        angle = 1;
        return angle;
    } else if (angle == 3 && af > 30) {
        angle = 4;
        return angle;
    } else if (angle == 4 && af < 45) {
        angle = 2;
        return angle;
    }

    // if (angle === 2) {
    //     let s1 = h('div', '').css('border', '1px solid').css('height', '1px').css('width', '1px');
    //     let s2 = h('div', '').css('border', '1px solid').css('height', '1px').css('width', '1px');
    //     document.body.appendChild(s1.el);
    //     document.body.appendChild(s2.el);
    //     debugger
    // }
    return angle;
};


const isAbsoluteValue = (str, rule = 1) => {
    str = str.toUpperCase();
    if (rule == 1) {
        if (str.search(/^\$[A-Z]+\$\d+$/) != -1)
            return 3;
        if (str.search(/^\$[A-Z]+\d+$/) != -1)
            return 1;
        if (str.search(/^[A-Z]+\$\d+$/) != -1)
            return 2;
        return false;
    } else if (rule == 3) {
        if (str.search(/^\$[A-Z]+\$\d+$/) != -1)
            return true;
        if (str.search(/^[A-Z]+\d+$/) != -1)
            return true;
        if (str.search(/^\$[A-Z]+\d+$/) != -1)
            return true;
        if (str.search(/^[A-Z]+\$\d+$/) != -1)
            return true;
        return false;
    } else if (rule == 4) {
        if (str.search(/^[A-Za-z]+\d+:[A-Za-z]+\d+$/) != -1)
            return true;
    } else if (rule == 5) {
        if (str.search(/^[A-Z]+\d+:\$[A-Z]+\d+$/) != -1) {
            return 8;
        }
        if (str.search(/^[A-Z]+\d+:[A-Z]+\$\d+$/) != -1) {
            return 9;
        }
        if (str.search(/^[A-Z]+\$\d+:[A-Z]+\d+$/) != -1) {
            return 10;
        }
        if (str.search(/^\$[A-Z]+\d+:[A-Z]+\d+$/) != -1) {
            return 11;
        }
        if (str.search(/^\$[A-Z]+\$\d+$/) != -1)
            return 3;
        if (str.search(/^[A-Z]+\d+$/) != -1)
            return 12;
        if (str.search(/^[A-Z]+\d+:[A-Z]+\d+$/) != -1)
            return 13;
        if (str.search(/^\$[A-Z]+\d+$/) != -1)
            return 1;
        if (str.search(/^[A-Z]+\$\d+$/) != -1)
            return 2;
        if (str.search(/^[A-Z]+\$\d+:[A-Z]+\$\d+$/) != -1) {
            return 4;
        }
        if (str.search(/^[A-Z]+\$\d+:\$[A-Z]+\d+$/) != -1) {
            return 5;
        }
        if (str.search(/^\$[A-Z]+\d+:[A-Z]+\$\d+$/) != -1) {
            return 6;
        }
        if (str.search(/^\$[A-Z]+\d+:\$[A-Z]+\d+$/) != -1) {
            return 7;
        }
        return false
    } else if (rule == 6) {
        str = str.replace(/\$/g, '');
        if (str.search(/^[A-Za-z]+\d+:[A-Za-z]+\d+$/) != -1)
            return true;

        return false;
    } else {
        if (str.search(/^[A-Za-z]+\d+$/) != -1)
            return true;
        if (str.search(/^[A-Za-z]+\d+:[A-Za-z]+\d+$/) != -1)
            return true;
        return false;
    }
};

