const splitStr = (str) => {
    let arr = str.split(/([(-\/,+*\s=^&])/);
    let arr2 = [];
    for (let i = 0; i < arr.length; i++) {
        let enter = 1;
        if (arr.length > i + 1) {
            let s2 = arr[i + 1];
            if (arr[i] === "(") {
                enter = 3;
            } else if (s2.indexOf("(") !== -1) {
                enter = 2;
            }
        }

        if (enter !== 3) {
            if (enter === 2) {
                arr2.push(arr[i] + "(");
            } else {
                arr2.push(arr[i]);
            }
        }
    }


    return arr2;
};

const cutting2 = (str, s) => {
    let arr = str.split(/([(-\/,+*\s=^&])/);

    let color = 0;
    let express = [];
    for (let i = 0; i < arr.length; i++) {
        if (arr[i]) {
            express.push(arr[i]);
        }
    }

    let colors = [];
    for (let i = 0; i < express.length; i++) {
        let s = express[i].toUpperCase();
        let enter = true;
        if (express.length > i + 1) {
            let s2 = express[i + 1];
            if (s2.indexOf("(") !== -1) {
                enter = false;
            }
        }

        if ((s.search(/^[A-Z]+\d+$/) != -1
                || s.search(/^\$[A-Z]+\$\d+$/) != -1
                || s.search(/^[A-Z]+\$\d+$/) != -1 || s.search(/^\$[A-Z]+\d+$/) != -1
                || s.search(/^[A-Za-z]+\d+:[A-Za-z]+\d+$/) != -1) && enter) {
            for (let i2 = 0; i2 < express[i].length; i2++)
                colors.push({
                    "code": color,
                    "data": express[i][i2],
                });
            color = color + 1;
        } else {
            let sc = s.replace(/\$/g, "");
            if (sc.search(/^[A-Za-z]+\d+:[A-Za-z]+\d+$/) != -1 && enter) {
                for (let i2 = 0; i2 < express[i].length; i2++)
                    colors.push({
                        "code": color,
                        "data": express[i][i2],
                    });
                color = color + 1;
            } else {
                for (let i2 = 0; i2 < express[i].length; i2++)
                    colors.push({
                        "code": -1,
                        "data": express[i][i2],
                    });
            }
        }

    }

    return colors;
};


function deepCopy(obj) {
    let result = Array.isArray(obj) ? [] : {};
    for (let key in obj) {
        if (obj.hasOwnProperty(key)) {
            if (typeof obj[key] === 'object' && obj[key] !== null) {
                result[key] = deepCopy(obj[key]);
            } else {
                result[key] = obj[key];
            }
        }
    }
    return result;
};

